/* const context=require.content() @params @params @params
context.resolve()context.keys()context.id
*/
const files = require.context("../components", true, /\.vue$/);
const components = {};
files.keys().forEach((url) => {
  let urlKey = url.replace(/.*\/(.*)\.vue$/, "$1"); //key
  let fileDefault = files(url).default; //export default 
  components[urlKey] = fileDefault;
});

const install = (Vue) => {
  for (let name in components) {
    Vue.component(name, components[name]);
  }
};
export default install;
